//     
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`product-tabpanel-${index}`}
      aria-labelledby={`product-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ py: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

const ProductGallery = ({ images, name }) => {
  const [mainImage, setMainImage] = useState(images[0]);
  
  return (
    <Box>
      {/*   */}
      <Box 
        className={`demo-image tea ${mainImage}`} 
        data-label={name}
        sx={{ 
          width: '100%',
          height: 400,
          mb: 2,
          borderRadius: 2,
          boxShadow: '0 6px 16px rgba(0,0,0,0.1)'
        }}
      />
      
      {/*   */}
      <Grid container spacing={2}>
        {images.map((img, index) => (
          <Grid item xs={3} key={index}>
            <Box 
              className={`demo-image tea ${img}`} 
              data-label=""
              sx={{ 
                width: '100%',
                height: 80,
                borderRadius: 1,
                opacity: mainImage === img ? 1 : 0.7,
                cursor: 'pointer',
                transition: 'all 0.3s ease',
                '&:hover': {
                  opacity: 0.9,
                  transform: 'scale(1.05)',
                }
              }}
              onClick={() => setMainImage(img)}
            />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

const ProductPage = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const { productId } = useParams();
  const navigate = useNavigate();
  
  //   
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [quantity, setQuantity] = useState(1);
  const [activeTab, setActiveTab] = useState(0);
  const [selectedImage, setSelectedImage] = useState(0);

  //    
  useEffect(() => {
    //     
    const fetchProduct = async () => {
      setLoading(true);
      
      //    ID
      const id = parseInt(productId, 10);
      const foundProduct = teasDatabase.find(p => p.id === id);
      
      if (foundProduct) {
        setProduct(foundProduct);
      } else {
        //    ,    404
        navigate('/404');
      }
      
      setLoading(false);
    };
    
    fetchProduct();
  }, [productId, navigate]);

  //  
  const handleQuantityChange = (action) => {
    if (action === 'increase') {
      setQuantity(prev => prev + 1);
    } else if (action === 'decrease' && quantity > 1) {
      setQuantity(prev => prev - 1);
    }
  };

  //   
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  //   
  const handleImageChange = (index) => {
    setSelectedImage(index);
  };

  //   ,  
  if (loading || !product) {
    return (
      <Container sx={{ py: 8 }}>
        <Typography variant="h4" align="center">
          ...
        </Typography>
      </Container>
    );
  }

  //   
  const relatedProducts = product.relatedProducts.map(id => 
    teasDatabase.find(p => p.id === id)
  ).filter(Boolean);

  return (
    <Box component="section" sx={{ py: 4 }}>
      <Container maxWidth="xl">
        {/*   */}
        <Breadcrumbs 
          separator={<NavigateNextIcon fontSize="small" />} 
          aria-label="breadcrumb"
          sx={{ mb: 3 }}
        >
          <MuiLink underline="hover" color="inherit" component={Link} to="/">
            
          </MuiLink>
          <MuiLink underline="hover" color="inherit" component={Link} to="/catalog">
            
          </MuiLink>
          <MuiLink 
            underline="hover" 
            color="inherit" 
            component={Link} 
            to={`/catalog?category=${product.category}`}
          >
            {product.category === 'black' ? ' ' : 
             product.category === 'green' ? ' ' : 
             product.category === 'oolong' ? '' : ''}
          </MuiLink>
          <Typography color="text.primary">{product.name}</Typography>
        </Breadcrumbs>

        {/*     */}
        <Grid container spacing={6}>
          {/*   */}
          <Grid item xs={12} md={6}>
            <motion.div
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
            >
              <Box sx={{ mb: 2 }}>
                <ProductGallery images={product.images} name={product.name} />
              </Box>
            </motion.div>
          </Grid>
          
          {/*    */}
          <Grid item xs={12} md={6}>
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
            >
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
                <Typography 
                  variant="body2" 
                  color="text.secondary"
                >
                  {product.category === 'black' ? ' ' : 
                   product.category === 'green' ? ' ' : 
                   product.category === 'oolong' ? '' : ''}
                </Typography>
                
                {product.isNew && (
                  <Chip 
                    label="" 
                    size="small" 
                    color="secondary" 
                    sx={{ fontWeight: 600, fontSize: '0.7rem' }}
                  />
                )}
              </Box>
              
              <Typography variant="h2" component="h1" sx={{ fontWeight: 700, mb: 2 }}>
                {product.name}
              </Typography>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <Rating value={product.rating} precision={0.5} readOnly />
                <Typography variant="body2" sx={{ ml: 1, color: 'text.secondary' }}>
                  ({product.reviewCount} )
                </Typography>
              </Box>
              
              <Typography variant="body1" paragraph sx={{ color: 'text.secondary', mb: 3 }}>
                {product.description}
              </Typography>
              
              <Typography variant="body1" sx={{ mb: 1 }}>
                <strong>:</strong> {product.origin}
              </Typography>
              
              <Typography variant="body1" sx={{ mb: 1 }}>
                <strong>:</strong> {product.weight}
              </Typography>
              
              <Box sx={{ my: 3 }}>
                <FeatureBox>
                  <VerifiedOutlinedIcon />
                  <Typography variant="body2">
                        
                  </Typography>
                </FeatureBox>
                
                <FeatureBox>
                  <LocalShippingOutlinedIcon />
                  <Typography variant="body2">
                       
                  </Typography>
                </FeatureBox>
                
                <FeatureBox>
                  <BalanceOutlinedIcon />
                  <Typography variant="body2">
                       
                  </Typography>
                </FeatureBox>
              </Box>
              
              <Divider sx={{ my: 3 }} />
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                <Typography variant="h4" component="p" color="primary" sx={{ fontWeight: 700 }}>
                  {product.price} 
                </Typography>
                
                {product.inStock ? (
                  <Chip 
                    label=" " 
                    size="small" 
                    color="primary" 
                    variant="outlined"
                    sx={{ ml: 2, fontWeight: 600 }}
                  />
                ) : (
                  <Chip 
                    label="  " 
                    size="small" 
                    color="error" 
                    variant="outlined"
                    sx={{ ml: 2, fontWeight: 600 }}
                  />
                )}
              </Box>
              
              {/*      */}
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mr: 3 }}>
                  <QuantityButton 
                    aria-label=" " 
                    onClick={() => handleQuantityChange('decrease')}
                    disabled={quantity <= 1}
                  >
                    <RemoveIcon />
                  </QuantityButton>
                  
                  <Typography sx={{ mx: 2, fontWeight: 600, minWidth: '30px', textAlign: 'center' }}>
                    {quantity}
                  </Typography>
                  
                  <QuantityButton 
                    aria-label=" " 
                    onClick={() => handleQuantityChange('increase')}
                  >
                    <AddIcon />
                  </QuantityButton>
                </Box>
                
                <Button 
                  variant="contained" 
                  color="primary" 
                  size="large"
                  startIcon={<AddShoppingCartIcon />}
                  disabled={!product.inStock}
                  sx={{ mr: 2, py: 1.5, px: 3 }}
                >
                   
                </Button>
                
                <IconButton color="primary" sx={{ mr: 1 }}>
                  <BookmarkBorderIcon />
                </IconButton>
                
                <IconButton color="primary">
                  <ShareIcon />
                </IconButton>
              </Box>
            </motion.div>
          </Grid>
        </Grid>

        {/*     */}
        <Box sx={{ my: 6 }}>
          <Paper sx={{ borderRadius: theme.shape.borderRadius * 2, overflow: 'hidden' }}>
            <Tabs
              value={activeTab}
              onChange={handleTabChange}
              variant={isMobile ? "scrollable" : "fullWidth"}
              scrollButtons={isMobile ? "auto" : false}
              sx={{ borderBottom: 1, borderColor: 'divider' }}
            >
              <Tab label="" id="product-tab-0" />
              <Tab label="  " id="product-tab-1" />
              <Tab label="" id="product-tab-2" />
              <Tab label="" id="product-tab-3" />
            </Tabs>
            
            <Box sx={{ p: 3 }}>
              <TabPanel value={activeTab} index={0}>
                <div dangerouslySetInnerHTML={{ __html: product.fullDescription }} />
              </TabPanel>
              
              <TabPanel value={activeTab} index={1}>
                <Typography variant="body1" paragraph>
                  {product.brewingRecommendations}
                </Typography>
              </TabPanel>
              
              <TabPanel value={activeTab} index={2}>
                <Typography variant="body1" paragraph>
                  {product.ingredients}
                </Typography>
              </TabPanel>
              
              <TabPanel value={activeTab} index={3}>
                <Typography variant="body1" paragraph>
                        .
                </Typography>
              </TabPanel>
            </Box>
          </Paper>
        </Box>

        {/*   */}
        <Box sx={{ mb: 6 }}>
          <Typography variant="h4" component="h2" sx={{ fontWeight: 700, mb: 4 }}>
               
          </Typography>
          
          <Grid container spacing={3}>
            {relatedProducts.map((item) => (
              <Grid item xs={12} sm={6} md={4} key={item.id}>
                <Card 
                  sx={{ 
                    height: '100%', 
                    display: 'flex', 
                    flexDirection: 'column',
                    transition: 'transform 0.3s ease, box-shadow 0.3s ease',
                    '&:hover': {
                      transform: 'translateY(-8px)',
                      boxShadow: '0 12px 20px rgba(0,0,0,0.15)',
                    },
                    borderRadius: theme.shape.borderRadius * 1.5,
                  }}
                >
                  <CardMedia
                    component="img"
                    height="200"
                    image={item.image}
                    alt={item.name}
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h6" component="h3" sx={{ fontWeight: 600 }}>
                      {item.name}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                      {item.description}
                    </Typography>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                      <Typography variant="h6" color="primary" sx={{ fontWeight: 700 }}>
                        {item.price} 
                      </Typography>
                      <Button 
                        component={Link} 
                        to={`/catalog/${item.id}`}
                        variant="outlined" 
                        size="small"
                      >
                        
                      </Button>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
      </Container>
    </Box>
  );
};

export default ProductPage; 