//   
const categories = [
  { id: 'all', name: ' ' },
  { id: 'black', name: ' ' },
  { id: 'green', name: ' ' },
  { id: 'oolong', name: '' },
  { id: 'herbal', name: ' ' },
  { id: 'fruit', name: ' ' },
  { id: 'premium', name: ' ' }
];

//  
const sortOptions = [
  { value: 'popularity', label: ' ' },
  { value: 'price_low', label: '  ( )' },
  { value: 'price_high', label: '  ( )' },
  { value: 'name_asc', label: '  (-)' },
  { value: 'name_desc', label: '  (-)' }
];

//      
const getCategoryName = (categoryId) => {
  const category = categories.find(cat => cat.id === categoryId);
  return category ? category.name : ' ';
};

const CatalogPage = () => {
  const theme = useTheme();
  const location = useLocation();
  
  //   category  URL
  const queryParams = new URLSearchParams(location.search);
  const categoryParam = queryParams.get('category');
  
  const [selectedCategory, setSelectedCategory] = useState(categoryParam || 'all');
  const [sortBy, setSortBy] = useState('popularity');
  const [searchQuery, setSearchQuery] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [filteredProducts, setFilteredProducts] = useState([]);
  
  const productsPerPage = 8;
  
  //    
  useEffect(() => {
    let result = [...productData];
    
    //   
    if (selectedCategory !== 'all') {
      result = result.filter(product => product.category === selectedCategory);
    }
    
    //    
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      result = result.filter(product => 
        product.name.toLowerCase().includes(query) || 
        product.description.toLowerCase().includes(query)
      );
    }
    
    //  
    switch (sortBy) {
      case 'price_low':
        result.sort((a, b) => a.price - b.price);
        break;
      case 'price_high':
        result.sort((a, b) => b.price - a.price);
        break;
      case 'name_asc':
        result.sort((a, b) => a.name.localeCompare(b.name));
        break;
      case 'name_desc':
        result.sort((a, b) => b.name.localeCompare(a.name));
        break;
      default: // popularity
        //     
        break;
    }
    
    setFilteredProducts(result);
    setCurrentPage(1); //       
  }, [selectedCategory, sortBy, searchQuery]);
  
  //   
  const handleCategoryChange = (event) => {
    setSelectedCategory(event.target.value);
  };
  
  //   
  const handleSortChange = (event) => {
    setSortBy(event.target.value);
  };
  
  //    
  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };
  
  //   
  const handlePageChange = (event, value) => {
    setCurrentPage(value);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };
  
  //     
  const indexOfLastProduct = currentPage * productsPerPage;
  const indexOfFirstProduct = indexOfLastProduct - productsPerPage;
  const currentProducts = filteredProducts.slice(indexOfFirstProduct, indexOfLastProduct);
  
  //    
  const totalPages = Math.ceil(filteredProducts.length / productsPerPage);
  
  //  
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };
  
  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.4 }
    }
  };

  return (
    <Box component="section" sx={{ py: 4 }}>
      <Container maxWidth="xl">
        {/*   */}
        <Breadcrumbs 
          separator={<NavigateNextIcon fontSize="small" />} 
          aria-label="breadcrumb"
          sx={{ mb: 2 }}
        >
          <Link underline="hover" color="inherit" href="/">
            
          </Link>
          <Typography color="text.primary"></Typography>
          {selectedCategory !== 'all' && (
            <Typography color="text.primary">{getCategoryName(selectedCategory)}</Typography>
          )}
        </Breadcrumbs>

        {/*    */}
        <CategoryBanner>
          <Container>
            <Typography 
              variant="h2" 
              component="h1" 
              sx={{ 
                fontWeight: 700, 
                textAlign: 'center',
                mb: 2
              }}
            >
              {selectedCategory === 'all' ? ' ' : getCategoryName(selectedCategory)}
            </Typography>
            <Typography 
              variant="h6" 
              sx={{ 
                maxWidth: 800, 
                mx: 'auto', 
                textAlign: 'center',
                opacity: 0.9
              }}
            >
                     . 
                  .
            </Typography>
          </Container>
        </CategoryBanner>

        {/*    */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} sm={6} md={3}>
            <FormControl fullWidth variant="outlined" size="small">
              <InputLabel></InputLabel>
              <Select
                value={selectedCategory}
                onChange={handleCategoryChange}
                label=""
              >
                {categories.map((category) => (
                  <MenuItem key={category.id} value={category.id}>
                    {category.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <FormControl fullWidth variant="outlined" size="small">
              <InputLabel></InputLabel>
              <Select
                value={sortBy}
                onChange={handleSortChange}
                label=""
              >
                {sortOptions.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              fullWidth
              variant="outlined"
              size="small"
              placeholder="    "
              value={searchQuery}
              onChange={handleSearchChange}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />
          </Grid>
        </Grid>

        {/*   */}
        <Box sx={{ mb: 2 }}>
          <Typography variant="body2" color="text.secondary">
             : {filteredProducts.length}
          </Typography>
        </Box>

        {/*   */}
        {filteredProducts.length > 0 ? (
          <motion.div 
            variants={containerVariants}
            initial="hidden"
            animate="visible"
          >
            <Grid container spacing={3}>
              {currentProducts.map((product) => (
                <Grid item xs={12} sm={6} md={4} lg={3} key={product.id}>
                  <motion.div variants={itemVariants}>
                    <ProductCard>
                      <ProductImage
                        image={product.image}
                        title={product.name}
                      />
                      <CardContent sx={{ flexGrow: 1 }}>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                          <Typography 
                            variant="body2" 
                            color="text.secondary"
                          >
                            {getCategoryName(product.category)}
                          </Typography>
                          {product.isNew && (
                            <Chip 
                              label="" 
                              size="small" 
                              color="secondary" 
                              sx={{ fontWeight: 600, fontSize: '0.7rem' }}
                            />
                          )}
                        </Box>
                        <Typography variant="h6" component="h2" sx={{ fontWeight: 600, mb: 1 }}>
                          {product.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                          {product.description}
                        </Typography>
                        <Typography variant="body2" sx={{ mb: 0.5 }}>
                          : {product.weight}
                        </Typography>
                        <Typography variant="h6" color="primary" sx={{ fontWeight: 700 }}>
                          {product.price} 
                        </Typography>
                      </CardContent>
                      <CardActions sx={{ p: 2, pt: 0 }}>
                        <Button 
                          variant="contained" 
                          color="primary" 
                          fullWidth
                          startIcon={<AddShoppingCartIcon />}
                        >
                           
                        </Button>
                      </CardActions>
                    </ProductCard>
                  </motion.div>
                </Grid>
              ))}
            </Grid>
          </motion.div>
        ) : (
          <Box sx={{ py: 5, textAlign: 'center' }}>
            <Typography variant="h5" gutterBottom>
                
            </Typography>
            <Typography variant="body1" color="text.secondary">
                     .
            </Typography>
          </Box>
        )}

        {/*  */}
        {totalPages > 1 && (
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 6, mb: 2 }}>
            <Pagination 
              count={totalPages} 
              page={currentPage} 
              onChange={handlePageChange} 
              color="primary"
              size="large"
            />
          </Box>
        )}
      </Container>
    </Box>
  );
};

export default CatalogPage; 